h1,h2,h3{margin-top: 0;padding: 0;}
#homepage_header_wraper{
	position:relative;
}

a.button {
	border-color: #000;
	border-width: 2px;
	font-weight: 700;
	padding-left:5px;
}
a.button:hover {
	border-color: #000;
	border-width: 2px;
	font-weight: 700;
}
a.button:before {
	display: inline-block;
	font: normal normal normal 14px/1 FontAwesome;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	transform: translate(0, 0);
	margin-right:5px;
	font-size: 26px;
	line-height: 23px;
	color:#000;
}

a.button.news,a.button.contact,a.button.docment{
	padding-left: 9px;
}
a.button.news:before{
	content: "\f1ea";
}
a.button.contact:before{
	content: "\f0e0";
}
a.button.docment:before{
	content: "\f07c";
}

