/* pager */
.builder-section-content.cycle-slideshow:after {
    height:85px;
    display:block;
    position:absolute;
    bottom:0;
    left:0;
    width:100%;
    z-index:999;
    background: linear-gradient(to bottom, rgba(222,222,222,0) 0%,rgba(222,222,222,.03) 5%,rgba(222,222,222,.85) 75%,rgba(222,222,222,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00dedede', endColorstr='#dedede',GradientType=0 );  /* IE6-9 */
    content:"";
}

.cycle-pager {
	text-align: center;
	width: 100%;
	z-index: 999999;
	position: absolute;
	bottom: 45px;
	overflow: hidden;
	/*background: #fff;*/
	height: 35px;
}
.cycle-pager span {
    font-family: arial;
    font-size: 50px;
    width: 16px;
    height: 16px;
    display: inline-block;
    background: #2941b2;
    cursor: pointer;
    border-radius: 16px;
    border: 2px solid #2941b2;
    margin-top: 6px;
	margin-right: 10px;
	box-shadow: 0 0 5px rgb(255,255,255);
	/*text-indent: -999em;*/
	color: rgba(136, 136, 136, 0);
}
.cycle-pager span.cycle-pager-active {
	background: #FFF;
	box-shadow: 0 0 5px rgb(0, 0, 0);
	border: 2px solid #941e3d;
}

.cycle-pager > * {
	cursor: pointer;
}


#binder{
	position:relative;
	box-shadow: rgb(0, 0, 0) 0 -2px 29px;
}

main {
	background: #dedede;
}

#content_area{
	box-shadow: inset 0 0 5px 2px rgba(50, 50, 50, 0.75);
	margin:5px 25px;
	box-sizing: border-box;
	color:#292929;
	z-index: 2;
	margin-top: -35px;
	font-size:16px;
	position: relative;
}

#footer{
	margin: 0 5px 13px 25px;
	font-size: 1.4em;
}
#footer ul li{
	display:inline-block;
}

div#main {
    padding: 5px 15px;
}

div#main h1{
    font-weight:900;
    font-size:2.5em;
    padding:0;
    margin: 0;
    margin-bottom: 10;
}

div#side_right {
	background: rgba(136, 136, 136, 0.45);
	color: #4A4A4A;
	box-shadow: inset 0 0 5px 2px rgba(50, 50, 50, 0.75);
}
div#side_left {
	background: rgba(186,186,186,0.45);
	box-shadow: inset 0 0 5px 2px rgba(50, 50, 50, 0.75);
}
div#side_left h2{
	font-size:1.7em;
	font-weight:600;
	border-bottom: 1px solid #333;
}

div#side_left ul, div#side_right ul{
	padding: 0 0 0 1.5em;
	margin: 0.65em 0;
}

div#side_left ul li, div#side_right ul li{
	font-size: 1.1em;
	font-weight: 400;
	line-height: 1.1em;
	margin-bottom: .25em;
	padding: 0 0 .25em 0;
	color: #4A4A4A;
}
div#side_right h3{
	font-size:1.5em;
	font-weight:600;
	color: #272727;
	border-bottom: 1px solid #272727;
}
div#side_right h4{
	font-size:1.5em;
	font-weight:600;
	color: #272727;
}

#new_home_page{
	background: rgba(186,186,186,0.45);
}
#new_home_page h3{
	font-size: 2em;
	font-weight: 600;
	border-bottom: 1px solid;
	margin-bottom: 5px;
}
#new_home_page h4{
	font-size: 1.7em;
	font-weight: 400;
	font-style: italic;
	margin-bottom: 5px;
	padding: 0;
	border-bottom: 4px solid;
}
.news-highlight-img{
	display: inline-block;
	/*float: left;*/
	margin-right: 10;
	margin-bottom: 7px;
}

#new_home_page p{
	font-size:1em;
	font-weight:400;
	font-style: italic;
}
span#fake_img {
	display: block;
	float: left;
	width: 258px;
	height: 185px;
	background: #1571B5;
}

div#border_top {
    background: #1122a3;
    height: 13px;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 9;
}
div#border_top:before {
    background: #494949;
    height: 7px;
    position: absolute;
    top: 17px;
    width: 100%;
    z-index: 9;
    content:"";
}
div#border_bottom {
    background: #1122a3;
    height: 13px;
    position: absolute;
    bottom: 0;
    width: 100%;
    z-index: 9;
}
div#border_bottom:before {
    background: #494949;
    height: 7px;
    position: absolute;
    bottom: 17px;
    width: 100%;
    z-index: 9;
    content:"";
}


/* Admin Bar */
@media (min-width: 990px) {
	.admin-bar #spine #glue {
		margin-top: 0px;
	}
	body.admin-bar {
		margin-bottom: -32px !important;
		/*padding-top: 32px !important;*/
	}
}

@media (max-width: 989px) {
	body.admin-bar {
		margin-top: 18px !important;
		padding-bottom: 0px !important;
	}

	#wpadminbar {
		top: auto !important;
		bottom: 0 !important;
		position: fixed !important;
	}

	#wpadminbar .quicklinks>ul>li {
		position: relative;
	}

	#wpadminbar .ab-top-menu>.menupop>.ab-sub-wrapper {
		bottom: 32px;
		box-shadow: none;
	}
}

@media (max-width: 780px) {

	body.admin-bar {
		margin-top: -46px !important;
		padding-bottom: 46px !important;
	}

	#wpadminbar .ab-top-menu>.menupop>.ab-sub-wrapper {
		bottom: 46px;
	}

}


